@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
*{font-family:'Nunito Sans', sans-serif;color:#fff;margin:0px}html{background-color:#202c37}.homepage,.detailpage,nav{padding:0px 80px}button:hover{cursor:pointer}nav{padding:22.5px 80px;background:#2b3945;display:flex;justify-content:space-between;box-shadow:0px 0px 8px 0px rgba(0,0,0,0.15)}nav h1{font-size:1.6rem;font-weight:600}nav button{font-size:1.1rem;border:none;background:transparent;font-weight:600}.homepage .search-bar{display:flex;justify-content:space-between;margin:50px 0px}.homepage .search-bar *{border:none;background-color:#2b3945;border-radius:5px}.homepage .search-bar .input-wrapper,.homepage .search-bar select{padding:16px 26px;box-shadow:0px 0px 5px 2px rgba(0,0,0,0.05)}.homepage .search-bar .input-wrapper :focus,.homepage .search-bar select :focus{outline:none}.homepage .search-bar .input-wrapper ::-webkit-input-placeholder, .homepage .search-bar select ::-webkit-input-placeholder{color:rgba(255,255,255,0.808)}.homepage .search-bar .input-wrapper :-ms-input-placeholder, .homepage .search-bar select :-ms-input-placeholder{color:rgba(255,255,255,0.808)}.homepage .search-bar .input-wrapper ::placeholder,.homepage .search-bar select ::placeholder{color:rgba(255,255,255,0.808)}.homepage .search-bar .input-wrapper{width:30%}.homepage .search-bar select{width:17%}.homepage .search-bar input{box-sizing:border-box;width:100%}.homepage .cards-grid{display:flex;flex-wrap:wrap;grid-gap:60px;gap:60px;padding-bottom:50px}.homepage .card{box-sizing:border-box;background-color:#2b3945;box-shadow:0px 0px 5px 2px rgba(0,0,0,0.05);width:calc(25% - 60px * 3 / 4);border-radius:7px;text-decoration:none}.homepage .card .card-info{box-sizing:border-box;padding:0px 25px 30px}.homepage .card .card-info .card-name{font-size:1.1rem;margin:15px 0px}.homepage .card .card-info p{font-size:0.85rem;margin:4px 0px}.homepage .card .card-info p .info{color:rgba(255,255,255,0.692)}.homepage .card .flag{width:100%}.homepage .card .flag img{width:100%;aspect-ratio:4.5/3;object-fit:fill;border-radius:7px 7px 0px 0px}.homepage .card:hover{cursor:pointer;box-shadow:0px 0px 10px 5px rgba(0,0,0,0.1)}.detailpage a{text-decoration:none}.detailpage button{padding:6px 30px;border:none;border-radius:3px;background-color:#2b3945;box-shadow:0px 0px 5px 2px rgba(0,0,0,0.05)}.detailpage button:hover{cursor:pointer;box-shadow:0px 0px 10px 2px rgba(0,0,0,0.15)}.detailpage .back{margin:60px 0px}.detailpage .info{display:flex;align-items:center;grid-gap:110px;gap:110px}.detailpage .info .flag{width:45%}.detailpage .info .flag img{aspect-ratio:4.5/3;object-fit:fill;width:100%;border-radius:5px}.detailpage .info .country-info{width:55%;display:flex;flex-wrap:wrap}.detailpage .info .country-info .country-name{width:100%;margin-bottom:25px}.detailpage .info .country-info .panel{width:50%}.detailpage .info .country-info .country-border{margin-top:45px;width:100%;display:flex;flex-wrap:wrap;align-items:center;grid-gap:10px;gap:10px}.detailpage .info .country-info .country-border *{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.detailpage .info .country-info p{margin:8px 0px}.detailpage .info .country-info p span{color:rgba(255,255,255,0.63)}@media (min-width: 2000px){html{font-size:18px}.homepage .card{width:calc(16.66% - 60px * 5 / 6)}.detailpage .info{grid-gap:300px;gap:300px}}@media (max-width: 1600px){html{font-size:18px}.detailpage .info{grid-gap:100px;gap:100px}}@media (max-width: 1200px){html{font-size:14px}.homepage,.detailpage,nav{padding-left:25px;padding-right:25px}.homepage .card{width:calc(33.33% - 60px * 2 / 3)}.detailpage .info{grid-gap:50px;gap:50px}}@media (max-width: 650px){.search-bar{flex-wrap:wrap;grid-gap:40px;gap:40px}.search-bar .input-wrapper,.search-bar select{flex-basis:100%}.homepage .card{width:calc(50% - 60px * 1 / 2)}.detailpage .info{flex-direction:column;grid-gap:30px;gap:30px}.detailpage .info .flag,.detailpage .info .country-info{width:100%}.detailpage .info .panel p{margin:10px 0px}}@media (max-width: 500px){html{font-size:18px}.homepage .card{width:100%}.detailpage .info .country-info{flex-direction:column}.detailpage .info .country-info .panel{width:100%}}

